import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TechStackComponent } from './tech-stack.component';
import { DjangoTechComponent } from './django-tech/django-tech.component';
import { CrossPlatformTechComponent } from './cross-platform-tech/cross-platform-tech.component';
import { CordovaTechComponent } from './cordova-tech/cordova-tech.component';
import { DotnetTechComponent } from './dotnet-tech/dotnet-tech.component';
import { EmberTechComponent } from './ember-tech/ember-tech.component';
import { GolangTechComponent } from './golang-tech/golang-tech.component';
import { IonicTechComponent } from './ionic-tech/ionic-tech.component';
import { IosTechComponent } from './ios-tech/ios-tech.component';
import { JavaTechComponent } from './java-tech/java-tech.component';
import { JavascriptTechComponent } from './javascript-tech/javascript-tech.component';
import { NodeTechComponent } from './node-tech/node-tech.component';
import { PhpTechComponent } from './php-tech/php-tech.component';
import { PythonTechComponent } from './python-tech/python-tech.component';
import { ReactTechComponent } from './react-tech/react-tech.component';
import { ReactNativeTechComponent } from './react-native-tech/react-native-tech.component';
import { RubyTechComponent } from './ruby-tech/ruby-tech.component';
import { ScalaTechComponent } from './scala-tech/scala-tech.component';
import { UnityTechComponent } from './unity-tech/unity-tech.component';
import { VueTechComponent } from './vue-tech/vue-tech.component';
import { XamarinTechComponent } from './xamarin-tech/xamarin-tech.component';
import { AndroidTechComponent } from './android-tech/android-tech.component';
import { AngularTechComponent } from './angular-tech/angular-tech.component';
import { BackboneTechComponent } from './backbone-tech/backbone-tech.component';
import { CLangTechComponent } from './c-lang-tech/c-lang-tech.component';
import { SecurityTechComponent } from './security-tech/security-tech.component';
import { AutomationTechComponent } from './automation-tech/automation-tech.component';
import { ManualTechComponent } from './manual-tech/manual-tech.component';



const routes: Routes = [
  {
    path: '',
    component: TechStackComponent,
    children: [
      {
        path: 'manual',
        component: ManualTechComponent
      },
      {
        path: 'android',
        component: AndroidTechComponent
      },
      {
        path: 'angular',
        component: AngularTechComponent
      },
      {
        path: 'backbone',
        component: BackboneTechComponent
      },
      {
        path: 'c-c-plus',
        component: CLangTechComponent
      },
      {
        path: 'cordova',
        component: CordovaTechComponent
      },
      {
        path: 'crossplatform',
        component: CrossPlatformTechComponent
      },
      {
        path: 'django',
        component: DjangoTechComponent
      },
      {
        path: 'dot-net',
        component: DotnetTechComponent
      },
      {
        path: 'ember-js',
        component: EmberTechComponent
      },
      {
        path: 'golang',
        component: GolangTechComponent
      },
      {
        path: 'ionic',
        component: IonicTechComponent
      },
      {
        path: 'ios',
        component: IosTechComponent
      },
      {
        path: 'java',
        component: JavaTechComponent
      },
      {
        path: 'javascript',
        component: JavascriptTechComponent
      },
      {
        path: 'node-js',
        component: NodeTechComponent
      },
      {
        path: 'php',
        component: PhpTechComponent
      },
      {
        path: 'python',
        component: PythonTechComponent
      },
      {
        path: 'react-js',
        component: ReactTechComponent
      },
      {
        path: 'react-native',
        component: ReactNativeTechComponent
      },
      {
        path: 'ruby',
        component: RubyTechComponent
      },
      {
        path: 'scala',
        component: ScalaTechComponent
      },
      {
        path: 'automation',
        component: AutomationTechComponent
      },
      {
        path: 'unity',
        component: UnityTechComponent
      },
      {
        path: 'vue-js',
        component: VueTechComponent
      },
      {
        path: 'xamarin',
        component: XamarinTechComponent
      },
      {
        path: 'security',
        component: SecurityTechComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TechStackRoutingModule { }
